import * as model from './model.js'; 
import RecipeView from './views/RecipeView.js';
import SearchView from './views/searchView.js';
import ResultView from './views/ResultView.js';
import PaginationView from './views/paginationView.js';

async function controlRecipes() {
  try {
    const parentEl = document.querySelector('.recipe');
    const recipeView = new RecipeView(parentEl);
    recipeView.renderSpinner();

    const id = window.location.hash.slice(1); // Obtener el ID de la receta del hash
    if (!id) return; // Si no hay hash, salir de la funci√≥n

    await model.loadRecipe(id);
    recipeView.render(model.state.recipe); 

  } catch (error) {
    RecipeView.renderError();
    throw error;
  }
}

const controlSearchResults = async function () {
  // Obtenemos la consulta del input de b√∫squeda
  const query = SearchView.getQuery();
  if (!query) return; // Si no hay consulta, no se contin√∫a

  try {
    ResultView.renderSpinner();

    await model.loadSearchResults(query); // Se cargan los resultados en el modelo

    ResultView.render(model.getSearchResultsPage()); // Renderizar la primera p√°gina de resultados
    PaginationView.render(model.state.search); // Renderizar botones de paginaci√≥n

    console.log('Resultados de b√∫squeda:', model.state.search.results);
  } catch (err) {
    console.error('Error al cargar resultados:', err);
  }
};

const controlPagination = function (goToPage) {
  model.state.search.page = goToPage; // Actualizar el n√∫mero de p√°gina
  ResultView.render(model.getSearchResultsPage(goToPage)); // Renderizar los resultados de la nueva p√°gina
  PaginationView.render(model.state.search); // Actualizar la paginaci√≥n
};

function init() {
  const recipeView = new RecipeView(document.querySelector('.recipe'));
  recipeView.addHandlerRender(controlRecipes);  

  // Usar la instancia searchView para llamar a addHandlerSearch
  SearchView.addHandlerSearch(controlSearchResults); 
  PaginationView.addHandlerClick(controlPagination); // Maneja la paginaci√≥n 
}

init();